html,
body {
  background-color: #f9f9f9;
  box-sizing: border-box;
  color: #444;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

header {
  background-color: #28324A;
  background-size: cover;
  margin: 0;
  padding: 0;
  position: relative;
  height: 100vh;
  width: 100%;
}

section {
  margin: 0;
  padding: 0;
  width: 100%;
}

h1 {
  font-size: 100px;
  font-family: 'Great Vibes', cursive;
  font-weight: 400;
}

h2 {
  font-family: 'Great Vibes', serif;
  font-size: 3.4rem;
  font-weight: 400;
  margin: 0 0 15px 0;
  text-align: center;
}

p {
  font-family: 'Raleway', sans-serif;
  line-height: 1.418;
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
}

#canvas-image-blending {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.container {
  padding: 80px 0;
  text-align: center;
}

.container-small {
  margin: 0 auto;
  padding: 80px 40px;
  text-align: center;
  width: 70%;
}

#events,
#things-to-do {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  min-height: 480px;
}

#map {
  grid-column: 1 / end;
  grid-row: 2;
  width: 100%;
  height: 380px;
  margin: 0 auto;
}

#photographs .container {
  padding-top: 60px;
}

nav.main {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat';
  height: 60px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.main-nav {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  display: inline-block;
  margin: 0 8px;
  position: relative;
}

.nav-item a {
  color: #fff;
  font-size: 16px;
  text-decoration: none;
}

.nav-item a::after {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background-color: #fff;
  position: absolute;
  left: 50%;
  bottom: -5px;
  transform: translateX(-50%);
  transition: width 0.4s;
}

.nav-item a:hover::after {
  width: 100%;
}

/*-----------------------------------------------------------------------*/

/*-------------------------GENERAL ELMNTS/STYLES-------------------------*/

/*-----------------------------------------------------------------------*/

:root {
  --blue: #28324A;
  --gold: #D6A319;
  --black: #444444;
  --white: #f9f9f9;
}

.btn {
  display: inline-block;
  padding: 15px 30px;
  margin: 40px auto 0;
  background-color: #D6A319;
  color: #FFFFFF;
  text-decoration: none;
  text-transform: uppercase;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------HEADER------------------------------*/

/*-----------------------------------------------------------------------*/

.names {
  color: #fff;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  top: 40%;
  transform: translateY(-50%);
  text-align: center;
}

.name {
  display: inline-block;
  margin: 0 20px;
}

.and {
  color: var(--gold);
  font-family: 'Great Vibes';
  font-weight: 400;
  font-size: 200px;
  margin: 10px 40px 0 20px;
}

.full-date {
  color: var(--white);
  font-size: 20px;
  font-family: 'Montserrat', cursive;
  font-weight: 200;
  letter-spacing: 6px;
  text-align: center;
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  width: 100%;
}

.down-arrow {
  height: 50px;
  width: 50px;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}

.down-arrow::after {
  content: '';
  background-color: var(--blue);
  width: 95%;
  height: 95%;
  position: absolute;
  top: 1px;
  left: 1px;
  opacity: 0.2;
  transform: rotate(45deg);
  z-index: -1;
  transition: all 0.3s;
}

.down-arrow:hover::after {
  opacity: 1;
}

.arrow {
  transition: all 0.2s;
}

.arrow:hover {
  transform: scale(1.2);
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------WELCOME------------------------------*/

/*-----------------------------------------------------------------------*/

.countdown {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.date {
  border-radius: 2px;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 30px 1fr;
  justify-items: center;
  width: 95px;
  height: 90px;
  background-color: #28324A;
  margin: 60px 10px 0;
  box-shadow: 4px 4px 18px rgba(0, 0, 0, 0.2), 2px 2px 12px rgba(0, 0, 0, 0.5);
}

.date-title {
  color: #FFEECA;
  align-self: end;
  font-family: 'Raleway';
  font-size: 16px;
  font-weight: 200;
  padding: 0;
  margin: 0;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}

[class*="--"] {
  font-family: 'Prata';
  font-size: 48px;
  align-self: end;
  color: #D6A319;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------EVENT------------------------------*/

/*-----------------------------------------------------------------------*/

.event-content {
  color: #FFEECA;
  text-align: center;
}

.event-content p {
  width: 70%;
  margin: 0 auto;
}

.event-img {
  background-image: url("https://source.unsplash.com/1600x900/?wedding,engagement");
  background-size: cover;
}

.schedule {
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
}

.event {
  letter-spacing: 0.5px;
  margin: 12px 0;
}

/*-----------------------------------------------------------------------*/

/*-------------------------------THINGS TO DO----------------------------*/

/*-----------------------------------------------------------------------*/

.boston {
  color: #FFEECA;
  background-image: linear-gradient(rgba(40, 50, 74, 0.8), rgba(75, 85, 99, 0.5)), url("https://source.unsplash.com/1600x900/?boston, city");
  background-size: cover;
}

.cape-cod {
  color: #FFEECA;
  background-image: linear-gradient(rgba(214, 123, 25, 0.6), rgba(40, 50, 74, 0.4)), url("https://source.unsplash.com/1600x900/?beach");
  background-size: cover;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------GALLERY-----------------------------*/

/*-----------------------------------------------------------------------*/

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 120px 120px 120px 120px;
  grid-template-areas: "lg-1 lg-1 sm-1 tl-1"
        "lg-1 lg-1 sm-2 tl-1"
        "tl-2 lg-2 lg-2 sm-3"
        "tl-2 lg-2 lg-2 sm-4";
  grid-gap: 10px;
  grid-auto-flow: dense;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 70%;
}

.gallery-item {
  background-color: #333;
  box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.6);
  overflow: hidden;
}

.gallery-item img {
  margin: 0;
  padding: 0;
  opacity: 0.73;
  width: 100%;
  height: 100%;
}

.img-lg-1 {
  grid-area: lg-1;
}

.img-lg-2 {
  grid-area: lg-2;
}

.img-tl-1 {
  grid-area: tl-1;
}

.img-tl-2 {
  grid-area: tl-2;
}

.img-sm-1 {
  grid-area: sm-1;
}

.img-sm-2 {
  grid-area: sm-2;
}

.img-sm-3 {
  grid-area: sm-3;
}

.img-sm-4 {
  grid-area: sm-4;
}

